<template>
  <div class="invite-friend">
    <div class="banner" :style="{backgroundImage: 'url(' + $assetsUrl + 'invite-friend/banner.png)'}">
      <div class="banner-info">
        <div class="info-left info">
          <p>{{peopleNum}} <span>人</span></p>
          <p class="bottom">邀請人數</p>
        </div>
        <div class="info-mid info">
          <p>{{flowNum}} <span>G</span></p>
          <p class="bottom">流量獎勵</p>
        </div>
        <div class="info-right info">
          <p>{{crashNum}} <span>元</span></p>
          <p class="bottom">現金分成</p>
        </div>
      </div>
    </div>
    <div class="introduce">
      <div class="card">
        <div class="card-top">
          <p class="first">每有一位好友使用你的邀請碼註冊<br/>你將獲得15G流量套餐半個月</p>
          <p class="second">如果您的好友續費套餐，更能您獲得<br />現金分成，最高能到達獲得40％</p>
        </div>
        <div class="line"></div>
        <div class="card-bottom" :style="{backgroundImage: 'url(' + $assetsUrl + 'invite-friend/step.png)'}"></div>
      </div>
    </div>
    <div class="copy-link" @click="copyLink()">
      复制邀请链接给好友
    </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';

export default {
  name: 'invite-friend',
  data() {
    return {
      inviteLink: '',
      code: '',
      peopleNum: '0',
      flowNum: '0',
      crashNum: '0.00',
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.$http.get(`${process.env.VUE_APP_SERVER_URL}/api/v1/affiliates`).then(
        (data) => {
          if (data.status === 200) {
            const originUrl = window.location.origin;
            this.inviteLink = `${originUrl}/invite/${data.data.code}`;
            this.code = data.data.code;
            this.peopleNum = data.data.visitors;
            this.crashNum = data.data.balance;
          }
        },
      );
    },
    copyLink() {
      if (!this.inviteLink) {
        return;
      }
      const invitation = `超快VPN服務，免費發送中！新用户注册后马上免费送一个月5G免费流量套餐，註冊時使用邀请码（${this.code}）更能再送半个月！点此领取免费流量：${this.inviteLink}`;
      Clipboard.copy(invitation);
      Toast({
        message: '複製成功',
        duration: 1000,
      });
    },
  },
};
</script>

